import { Component, Inject, OnInit } from '@angular/core';
import { IRubro } from '../../models/rubro';
import { RubrosService } from '../../service/rubros.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
    selector: 'app-save-rubros',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    columns = ['nombre', 'activo', 'acciones'];
    form: FormGroup;
    rubro: IRubro;
    title: string;
    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private rubrosService: RubrosService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.rubro = this.data ? this.data : null;
        this.title = this.rubro?.idrubro ? 'Editar rubro' : 'Nuevo rubro';
    }

    ngOnInit(): void {
        this.newForm();
    }

    newForm() {
        this.form = new FormGroup({
            descripcion: new FormControl((this.rubro ? this.rubro.descripcion : ''), Validators.required),
            activo: new FormControl((this.rubro ? (this.rubro?.activo ? 1 : 0) : ''), Validators.required),
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.rubro = {
                ...this.rubro,
                descripcion: this.form.get('descripcion').value,
                activo: this.form.get('activo').value,
            }
            if (this.rubro.idrubro) {
                this.rubrosService.updateRubro(this.rubro)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            } else {
                this.rubrosService.saveRubro(this.rubro)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close()
    }
}